let pagina = 1;

const cita = {
    nombre: '',
    fecha: '',
    hora:'',
    servicios:[]
}

document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp(){
    mostrarServicios();

    //resalta el div actual al que se presiona
    mostrarSeccion();

    // oculta o muestra una seccion segun el tab al que se presiona
    cambiarSeccion();

    //paginacion siguiente y anterior
    paginaSiguiente();

    paginaAnterior();

    //comprueba la pagina actual para ocultar o mostrar la paginacion
    botonesPaginador();

    //muestra el resumen de la cita(o mensaje de error en caso de no pasar validacion)
    mostrarResumen();

    //almacena el nombre de la cita en el objeto
    nombreCita();


    //almacena la fecha de la cita en el objeto
    fechaCita();


    //deshabilita dias pasados
    deshabilidarFechaanterior();

    //almacena la hora de la cita en el objeto
    horaCita();
}

function mostrarSeccion(){
    //eliminar mostrar seccion de la seccion anterior
    const seccionAnterior = document.querySelector('.mostrar-seccion');
    if(seccionAnterior){
        seccionAnterior.classList.remove('mostrar-seccion');
    }

    const seccionActual = document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('mostrar-seccion');

    //eliminar la clase de actual en el tab anterior
    const tabAnterior = document.querySelector('.tabs .actual');
    if(tabAnterior){
        tabAnterior.classList.remove('actual');
    }
    



    //resaltar tab actual
    const tab = document.querySelector(`[data-paso="${pagina}"]`);
    tab.classList.add('actual');
}

function cambiarSeccion(){
    const enlaces = document.querySelectorAll('.tabs button');

    enlaces.forEach( enlace =>{
        enlace.addEventListener('click', e => {
            e.preventDefault();
            pagina = parseInt(e.target.dataset.paso);



            // llamar la seccion de mostrar seccion
            mostrarSeccion();
            botonesPaginador();
        })
    })

};


async function mostrarServicios(){
    try {
        const resultado = await fetch('./servicios.json')
        const db = await resultado.json();

        const {servicios} = db;

        //generar el html
        servicios.forEach( servicio => {
            const {id,nombre,precio} = servicio;

            //dom scripting
            //generar nombre servicio
            const nombreServicio = document.createElement('P');
            nombreServicio.textContent = nombre;
            nombreServicio.classList.add('nombre-servicio');

            //generar precio servicio
            const precioServicio = document.createElement('P');
            precioServicio.textContent = `$ ${precio}`; //se necesita un template string cuando hagamos listas con precios y signo de dolar
            precioServicio.classList.add('precio-servicio');
            

            // generar div contenedor de servicio
            const servicioDiv = document.createElement('DIV');
            servicioDiv.classList.add('servicio');
            servicioDiv.dataset.idServicio = id;


            // selecciona un servicio para la cita
            servicioDiv.onclick = seleccionarServicio;

            // inyectar precio y nombre al div de servicio
            servicioDiv.appendChild(nombreServicio);
            servicioDiv.appendChild(precioServicio);
            

            //inyectar en el html
            document.querySelector('#servicios').appendChild(servicioDiv);


        })


    
    } catch (error) {
        console.log('Error');
    }
}

function seleccionarServicio(e){
    //forzar que el elemento que al que le damos clic sea el div
    let elemento;

    if(e.target.tagName == 'P'){
        elemento = e.target.parentElement;
    }
    else{
        elemento = e.target;
    }

    if(elemento.classList.contains('seleccionado')){ // si contiene esa clase al darle click y si no la tiene se la da
        elemento.classList.remove('seleccionado');

        const id = parseInt(elemento.dataset.idServicio);
        eliminarServicio(id);
    }
    else{
        elemento.classList.add('seleccionado');

        

        const servicioObj = {
            id: parseInt(elemento.dataset.idServicio),
            nombre: elemento.firstElementChild.textContent,
            precio: elemento.firstElementChild.nextElementSibling.textContent
        }

        // console.log(servicioObj);
        agregarServicio(servicioObj);      
    }
    
    
}

function eliminarServicio(id){
    const {servicios} = cita;
    cita.servicios = servicios.filter(servicio => servicio.id !== id);

    console.log(cita);

};

function agregarServicio(servicioObj ){
    const {servicios} = cita;
    cita.servicios = [...servicios,servicioObj];

    console.log(cita);
}

function paginaSiguiente(){
    const paginaSiguiente = document.querySelector('#siguiente');
    paginaSiguiente.addEventListener('click', ()=>{
        pagina++;
        botonesPaginador();
    })
}

function paginaAnterior(){
    const paginaAnterior = document.querySelector('#anterior');
    paginaAnterior.addEventListener('click', ()=>{
        pagina--;
        botonesPaginador();
    })
}

function botonesPaginador(){
    const paginaSiguiente = document.querySelector('#siguiente');
    const paginaAnterior = document.querySelector('#anterior');

    if(pagina == 1){
        paginaAnterior.classList.add('ocultar');
        paginaSiguiente.classList.remove('ocultar');

    }
    else if(pagina === 2){
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }
    else if(pagina === 3){
        paginaSiguiente.classList.add('ocultar');
        paginaAnterior.classList.remove('ocultar');
        mostrarResumen();//estamos en la pagina 3, carga el resumen de la cita

    }

    mostrarSeccion();// cambia la seccion por la de la pagina
}


function mostrarResumen(){
    //destructuring
    const {nombre,fecha,hora,servicios} = cita;

    //seleccionar el resumen
    const resumenDiv = document.querySelector('.contenido-resumen');


    //limpia el html previo
    while(resumenDiv.firstChild){
        resumenDiv.removeChild(resumenDiv.firstChild);
    }


    //validacion de objeto
    if(Object.values(cita).includes('')){
        const noServicios = document.createElement('P');
        noServicios.textContent = 'Faltan Datos de Servicios, Hora, Fecha o Nombre';
        noServicios.classList.add('invalidar-cita');

        //agregar resumen div
        resumenDiv.appendChild(noServicios);
    }else{

        const headingCita = document.createElement('H3');
        headingCita.textContent = 'Resumen de Cita';

        const nombreCita = document.createElement('P');
        nombreCita.innerHTML = `<span>Nombre:</span> ${nombre}`;

        const fechaCita = document.createElement('P');
        fechaCita.innerHTML = `<span>Fecha:</span> ${fecha}`;

        const horaCita = document.createElement('P');
        horaCita.innerHTML = `<span>Hora:</span> ${hora}`;

        

        const serviciosCita = document.createElement('DIV');
        serviciosCita.classList.add('resumen-servicios');

        const headingServicios = document.createElement('H3');
        headingServicios.textContent = 'Resumen de Servicios';

        serviciosCita.appendChild(headingServicios);

        //iterar sobre el arreglo de servicios

        let cantidad = 0;
        servicios.forEach( servicio =>{
            const {nombre,precio} = servicio;
            const contenedorServicio = document.createElement('DIV');
            contenedorServicio.classList.add('contenedor-servicio');

            const textoServicio = document.createElement('P');
            textoServicio.textContent = nombre;

            const precioServicio = document.createElement('P');
            precioServicio.textContent = precio;
            precioServicio.classList.add('precio');

            const totalServicio = precio.split('$');

            cantidad += parseInt(totalServicio[1].trim());


            //colocar texto y precio en el div

            contenedorServicio.appendChild(textoServicio);
            contenedorServicio.appendChild(precioServicio);

            serviciosCita.appendChild(contenedorServicio);

        });

        console.log(cantidad);

        resumenDiv.appendChild(headingCita);
        resumenDiv.appendChild(nombreCita);
        resumenDiv.appendChild(fechaCita);
        resumenDiv.appendChild(horaCita);

        resumenDiv.appendChild(serviciosCita);

        const cantidadPagar = document.createElement('P');
        cantidadPagar.classList.add('total');
        cantidadPagar.innerHTML = `<span>Total a pagar: </span> $  ${cantidad}`;

        resumenDiv.appendChild(cantidadPagar);


        
    }
}



function nombreCita(){
    const nombreInput = document.querySelector('#nombre');

    nombreInput.addEventListener('input', e => {
        const nombreTexto = e.target.value.trim();//trim elimina los espacios del inicio y final que pueda dar el usuario
        

        //validacion de nombreTexto debe tener algo

        if(nombreTexto === '' || nombreTexto.lenght <3){ //.lenght tamaño del caracter escrito
            mostrarAlerta('nombre no valido', 'error');
        }
        else{
            const alerta = document.querySelector('.alerta');
            if(alerta){
                alerta.remove();
            }
            cita.nombre = nombreTexto;
            
        }
    });
}


function mostrarAlerta(mensaje,tipo){
    
    //si hay una alerta previa, entonces no crear otra
    const alertaPrevia = document.querySelector('.alerta');

    if(alertaPrevia){
        return;//la funcion deja de ejecutarse
    }

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');

    if(tipo === 'error'){
        alerta.classList.add('error');
    }

    console.log(alerta);

    // insertar en el html

    const formulario = document.querySelector('.formulario');
    formulario.appendChild(alerta);


    //eliminar la alerta despues de los 3 segundos

    setTimeout(() =>{
        alerta.remove();
    }, 3000);
}



function fechaCita(){
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('input', e =>{
        const dia = new Date(e.target.value).getUTCDay();//arreglo que nos ayudará a saber en que dia de la semana estamos parados
        //console.log(dia);

    
        if([0,6].includes(dia)){
            e.preventDefault();
            fechaInput.value = '';
            mostrarAlerta('Fines de semana no son permitidos' , 'error');
        }
        else{
            cita.fecha = fechaInput.value;

            console.log(cita);
        }
    })
}


function deshabilidarFechaanterior(){
    const inputFecha = document.querySelector('#fecha');

    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const mes = fechaAhora.getMonth() +1;
    const dia = fechaAhora.getDate();
        // formato deseado: aaaa--mm-dd

    const fechaDeshabilitar = `${year}-${mes}-${dia}`;
    inputFecha.min = fechaDeshabilitar;
}



function horaCita(){
    const inputHora = document.querySelector('#hora');
    inputHora.addEventListener('input', e =>{
    

        const horaCita = e.target.value;
        const hora = horaCita.split(':')//nos permite evaluar y dividir una cadena de texto

        if(hora[0] < 10 || hora[0] >18){
            mostrarAlerta('Hora no válida','error');
            setTimeout(() => {
                inputHora.value = '';
            }, 3000);
            
        }else{
            cita.hora = horaCita;
        }

    });
}